@charset "UTF-8";
/* /////////////////////////////// //  БАЗОВЫЕ СТИЛИ /////////////////////////////// */
@font-face {
  font-family: Montserrat;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: Montserrat;
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: Montserrat;
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}
:root {
  --text-color: #333333;
  --color-white: #ffffff;
  --text-gray-color: #3d3d3d;
  --text-black-color: #000000;
  --primary-color: #ff8125;
  --primary-color-500: #f1631c;
  --primary-yellow: #fec224;
  --grey-200: #ececec;
  --border-color: #e0eff5;
  --border-radius: 4px;
  --box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
}

body {
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  color: var(--text-color);
  font-size: 16px;
  font-weight: 700;
  height: auto;
  background: transparent;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
  outline: 0;
}

ul {
  list-style: none;
}

button {
  line-height: 100%;
  font-size: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  cursor: pointer;
  border: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.container {
  box-sizing: border-box;
  max-width: 1230px;
  padding: 0 24px;
  margin: 0 auto;
  position: relative;
}

@media (min-width: 667px) {
  .container {
    padding: 0 24px;
  }
}
@media (min-width: 767px) {
  .container {
    padding: 0 50px;
    min-width: 320px;
  }
}
@media (min-width: 1240px) {
  .container {
    padding: 0 30px;
  }
}
.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  overflow-x: hidden;
}

.wrapper.modal-open::after {
  position: fixed;
  z-index: 30;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  content: "";
  background-color: rgba(0, 0, 0, 0.7);
}

.wrapper main {
  flex: 1 0 auto;
  width: 100%;
}

@media (min-width: 767px) {
  .wrapper::before {
    content: "";
    position: absolute;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
    min-width: 1920px;
    aspect-ratio: 1920/966;
    min-height: 1188px;
    height: auto;
    top: 0;
    background-repeat: no-repeat;
    background-image: image-set(url("../img/bg.webp") 1x, url("../img/bg.png") 1x);
    background-position: top center;
    background-size: 100% 100%;
  }
}
@media (min-width: 767px) and (min-width: 1120px) {
  .wrapper::before {
    min-height: 960px;
  }
}
@media (min-width: 767px) and (min-width: 1340px) {
  .wrapper::before {
    aspect-ratio: 1920/1188;
    min-height: 1188px;
  }
}
.section {
  text-align: center;
}

.section h2 {
  font-weight: 700;
  font-size: clamp(1.2rem, 3vw, 2.75rem);
  line-height: 3.375rem;
}

.section h2 + p {
  font-weight: 700;
  font-size: clamp(1rem, 2.5vw, 1.125rem);
  line-height: 22px;
}

.app-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 3px;
  border: 0;
  outline: 0;
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 1.5rem;
  color: var(--color-white);
  padding: 5px 15px;
}

@media (hover: hover), screen and (min-width: 0\0 ) {
  .app-button:hover {
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    transform: translateY(-3px);
  }

  .app-button:active {
    box-shadow: none;
    transform: translateY(0);
  }
}
.app-button--primary {
  background: linear-gradient(180deg, #c24e13 -16%, #f6651d 100%);
}

.app-button--secondary {
  border: 3px solid #ffffff;
  background-color: transparent;
}

.app-button--ghost {
  border: 3px solid var(--primary-color-500);
  color: var(--primary-color-500);
  background-color: transparent;
}

@media (hover: hover), screen and (min-width: 0\0 ) {
  .app-button--ghost:active {
    color: var(--color-white);
    background: linear-gradient(180deg, #ff8447 0, #ff5500 100%);
  }
}
.site-breadcrumbs {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.site-breadcrumbs__wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.site-breadcrumbs__wrap a:first-child {
  position: relative;
  padding-left: 32px;
}

@media (min-width: 767px) {
  .site-breadcrumbs__wrap a:first-child {
    padding-left: 0;
  }
}
.site-breadcrumbs__wrap a:first-child::before {
  content: "";
  left: 0;
  position: absolute;
  margin-right: 12px;
  width: 20px;
  height: 20px;
  background-image: url("data:image/svg+xml,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.4163 20.8333V14.5833H14.583V20.8333H19.7913V12.5H22.9163L12.4997 3.125L2.08301 12.5H5.20801V20.8333H10.4163Z' fill='%23666666'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-size: cover;
}

@media (min-width: 767px) {
  .site-breadcrumbs__wrap a:first-child::before {
    position: relative;
    width: 27px;
    height: 27px;
    margin-right: 27px;
    flex: 0 0 27px;
  }
}
.site-breadcrumbs__wrap a {
  font-size: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (min-width: 767px) {
  .site-breadcrumbs__wrap a {
    font-size: 0.875rem;
  }
}
.site-breadcrumbs__wrap a:hover {
  text-decoration: underline;
}

.site-breadcrumbs__wrap a:not(:first-child)::before {
  margin: 0px;
  content: "";
  background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' viewBox='0 0 15 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.36914 10.3625L8.23164 7.5L5.36914 4.63125L6.25039 3.75L10.0004 7.5L6.25039 11.25L5.36914 10.3625Z' fill='black'/%3E%3C/svg%3E%0A");
  display: block;
  width: 15px;
  height: 15px;
}

@media (min-width: 767px) {
  .site-breadcrumbs__wrap a:not(:first-child)::before {
    margin: 0 22px;
  }
}
.f-faq {
  background: #f2f2f2;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25);
}

.f-faq__item {
  position: relative;
  border-bottom: 1px solid #e9e8e8;
}

.f-faq__item summary {
  display: flex;
  align-items: center;
}

.f-faq__item summary::before {
  display: inline-block;
  content: "Q";
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  margin: 0 22px;
}

.f-faq__item summary::marker, .f-faq__item summary::-webkit-details-marker {
  display: none;
}

.f-faq__item summary:hover {
  cursor: pointer;
}

.f-faq__item summary span {
  padding: 0.8rem;
  display: flex;
  align-items: center;
  width: 100%;
  position: relative;
}

@media (min-width: 667px) {
  .f-faq__item summary span {
    padding: 10px 24px 13px 34px;
  }
}
.f-faq__item summary span::after {
  content: "+";
  margin-left: 0.5rem;
  width: 32px;
  height: 32px;
  flex: 0 0 32px;
  background: #d9d9d9;
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-right: 0;
}

@media (min-width: 667px) {
  .f-faq__item summary span::after {
    margin-left: auto;
  }
}
.f-faq__item summary span::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  height: 100%;
  background: #000000;
  justify-self: stretch;
}

.f-faq__item-icon {
  transition: all 0.1s ease 0s;
}

.f-faq__item[open] > summary {
  color: var(--text-black-color);
  border-bottom: 1px solid #000000;
}

.f-faq__item[open] > summary::after {
  transform: rotate(180deg);
  transform-origin: center;
}

.f-faq__item-content {
  padding: 1.25rem;
}

.custom-select {
  position: relative;
  display: inline-block;
  width: 100%;
}

@media (hover: hover), screen and (min-width: 0\0 ) {
  .custom-select:hover .custom-select__menu {
    opacity: 1;
    visibility: visible;
    z-index: 999;
  }

  .custom-select:hover .custom-select__trigger::after {
    transform: rotate(180deg);
  }
}
.custom-select.isOpen {
  z-index: 5;
}

.custom-select.isOpen .custom-select__menu {
  opacity: 1;
  visibility: visible;
  z-index: 1;
}

.custom-select.isOpen .custom-select__trigger::after {
  transform: rotate(180deg);
}

.custom-select.isOpen .custom-select__backdrop {
  display: block;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  left: 0;
  background-color: rgba(80, 80, 80, 0.7);
  z-index: -1;
}

.custom-select__trigger {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.custom-select__trigger::after {
  content: "";
  position: relative;
  display: block;
  width: 19px;
  height: 11px;
  background-repeat: no-repeat;
  background-image: image-set(url("../img/down-arrow.webp") 1x, url("../img/down-arrow.png") 1x);
  background-size: contain;
}

.custom-select__trigger::before {
  position: absolute;
  top: 100%;
  left: 0;
  content: "";
  width: 100%;
  height: 40px;
  user-select: none;
  background: transparent;
}

.custom-select__trigger--white::after {
  background-image: image-set(url("../img/down-arrow-white.webp") 1x, url("../img/down-arrow-white.png") 1x);
  background-repeat: no-repeat;
  width: 12px;
  height: 7px;
  background-size: contain;
}

.custom-select__menu {
  transition: all 0.3s ease 0s;
  padding: 0;
  margin: 0;
  box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.2);
  position: absolute;
  width: 100%;
  height: auto;
  top: calc(100% + 28px);
  left: 0;
  opacity: 0;
  visibility: hidden;
  display: grid;
  grid-template-columns: 1fr;
  align-items: start;
  background-color: rgba(0, 0, 0, 0.2);
}

.custom-select__menu::after {
  content: "";
  position: absolute;
  top: -10px;
  right: 15px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 10px 10px 10px;
  border-color: transparent transparent rgba(0, 0, 0, 0.2) transparent;
}

.custom-select__item {
  text-align: left;
  font-size: 17px;
  line-height: 2.5;
  color: #ffffff;
}

.custom-select__item:not(:last-child) {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.custom-select__item a {
  padding-left: 30px;
  display: block;
  width: 100%;
}

[data-tabs=parent] {
  overflow: hidden;
}

[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}

[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.publish-card {
  position: relative;
  background: #ffffff;
  border: 1px solid var(--primary-color-500);
  border-radius: 4px;
  padding: 1.25rem 1.1875rem 2.375rem;
  text-align: center;
}

.publish-card .app-button {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  padding: 7px;
  width: 140px;
  background: linear-gradient(180deg, #ff8447 0%, #ff5500 100%);
}

.publish-card__img-wrap {
  border-radius: 4px;
  overflow: hidden;
  width: 226px;
  aspect-ratio: 226/138;
  height: auto;
  width: 100%;
  max-width: 226px;
  margin-inline: auto;
  position: relative;
  margin-bottom: 1.5rem;
}

.publish-card__img-wrap img {
  border-radius: 4px;
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: contain;
}

.publish-card__title {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: var(--text-black-color);
  margin-bottom: 2.1875rem;
}

.lang-select {
  position: relative;
  cursor: pointer;
}

.lang-select:not(.active) .lang-select__options-list {
  opacity: 0;
  visibility: hidden;
}

.lang-select:not(.active) .lang-select__selected-text::after {
  transform: rotate(0deg);
}

.lang-select__selected-value {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}

.lang-select__selected-img-wrap {
  border: 2px solid var(--primary-color);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  padding: 3px;
}

.lang-select__selected-img-wrap img {
  display: flex;
  border-radius: 50%;
  overflow: hidden;
}

.lang-select__selected-text {
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  display: flex;
  align-items: center;
  gap: 5px;
}

.lang-select__selected-text::after {
  transition: transform 0.3s ease 0s;
  transform-origin: center;
  transform: rotate(180deg);
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  background-image: url("../img/icons/icon-arrow.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

.lang-select__options-list {
  position: absolute;
  border: 2px solid var(--color-white);
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transition: all 0.3s ease 0;
  background-color: rgba(0, 0, 0, 0.4);
  box-shadow: 0 1px 6px 0 rgba(32, 33, 36, 0.28);
}

.lang-select__option a {
  display: flex;
  cursor: pointer;
  padding: 0.7rem 1.5rem;
  font-size: 0.875rem;
}

.lang-select__option a:hover {
  background-color: rgba(0, 0, 0, 0.4);
}

.form {
  display: grid;
  gap: 1.25rem;
  background-color: var(--color-white);
  padding: 1rem;
}

@media (min-width: 667px) {
  .form {
    padding: 75px 35px 40px;
  }
}
.form__row-2 {
  display: grid;
  gap: 1.5rem;
}

@media (min-width: 667px) {
  .form__row-2 {
    grid-template-columns: repeat(2, 1fr);
  }
}
.form__input {
  background: #f2f2f2;
  border-radius: 4px;
  border: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  outline: 0;
  padding: 13px 20px;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  width: 100%;
  color: #666666;
}

.form__input::placeholder {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  font-weight: inherit;
  color: #a4a4a4;
}

.form__input--textarea {
  resize: none;
  height: 173px;
}

.form__button-submit {
  padding-block: 16px;
  background: radial-gradient(90.91% 90.91% at 27.47% 90.91%, #c7551d 0%, #ff8b52 95.92%);
}

.logo__word {
  font-weight: 400;
  font-size: 1.875rem;
  line-height: 1;
  margin-right: 0.5rem;
}

.logo__nums {
  font-size: 1.875rem;
  font-weight: 700;
  background: linear-gradient(180deg, #e15f1e 25%, #ff5500 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.contact-feat-list {
  display: grid;
  gap: 1rem;
  text-align: left;
}

.contact-feat-list__item {
  background-color: var(--color-white);
  padding: 14px 26px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.contact-feat-list__item::before {
  content: "";
  display: block;
  flex: 0 0 51px;
  width: 51px;
  height: 51px;
  background-size: cover;
  background-repeat: no-repeat;
  margin-right: 40px;
}

.contact-feat-list__item--prices::before {
  background-image: url("../img/contact/feat-1.svg");
}

.contact-feat-list__item--premium::before {
  background-image: url("../img/contact/feat-2.svg");
}

.contact-feat-list__item--traffic::before {
  background-image: url("../img/contact/feat-3.svg");
}

.contact-feat-list__item--target::before {
  background-image: url("../img/contact/feat-4.svg");
}

.contact-feat-list__item--brand::before {
  background-image: url("../img/contact/feat-5.svg");
}

.menu-button {
  display: none;
}

@media (max-width: 1024px) {
  .menu-button {
    width: 44px;
    height: 44px;
    display: grid;
    place-items: center;
    border: 0;
    outline: 0;
    background: transparent;
  }
}
.menu-button__icon {
  width: 22.5px;
  height: 1px;
  display: block;
  background: #ffffff;
  position: relative;
}

.menu-button__icon::after, .menu-button__icon::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background: #ffffff;
  left: 0;
  transition: transform 0.3s ease 0s;
}

.menu-button__icon::after {
  transform-origin: left bottom;
  bottom: 6px;
}

.menu-button__icon::before {
  transform-origin: left top;
  top: 6px;
}

.menu-button[aria-expanded=true] .menu-button__icon {
  height: 0;
  width: 30px;
}

.menu-button[aria-expanded=true] .menu-button__icon::after, .menu-button[aria-expanded=true] .menu-button__icon::before {
  background: #ffffff;
}

.menu-button[aria-expanded=true] .menu-button__icon::after {
  transform: translate(2px, -4px) rotate(45deg);
}

.menu-button[aria-expanded=true] .menu-button__icon::before {
  transform: translate(2px, 4px) rotate(-45deg);
}

.header {
  background: rgba(57, 57, 57, 0.85);
  color: #ffffff;
  position: relative;
  z-index: 1;
  width: 100%;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
}

@media (min-width: 667px) {
  .header {
    padding: 15px 0;
  }
}
@media (min-width: 767px) {
  .header {
    background: transparent;
  }
}
.header .logo {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
}

.header__container {
  max-width: 1424px;
  padding: 1rem;
  gap: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin-inline: auto;
}

@media (min-width: 667px) {
  .header__container {
    padding: 2.5625rem 1rem;
  }
}
@media (max-width: 1024px) {
  .header__container {
    justify-content: space-between;
  }
}
.header-menu {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

@media (max-width: 1024px) {
  .header-menu .header-menu__button {
    z-index: 100;
    background: transparent;
  }
}
.header-menu__logo-wrap {
  margin-right: 50px;
  display: flex;
  align-items: center;
}

@media (max-width: 850px) {
  .header-menu__logo-wrap {
    margin-right: 0;
  }
}
.header-menu__list {
  flex: 0 1 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

.header-menu__item {
  padding: 0 10px;
}

.header-menu__item:not(:last-child) {
  margin-right: 11px;
}

@media (max-width: 1024px) {
  .header-menu__item {
    margin: 0;
  }
}
