$min-width: 320 + px;
$max-width: 1230 + px;
$width-mobile: 320 + px;
$width-mobile-h: 667 + px;
$width-tablet: 767 + px;
$width-tablet-h: 1240 + px;
$width-desktop: 1340 + px;

$transition: 0.3s ease-out 0s;
$font-family: "Montserrat", "Arial", sans-serif;
$fz-default: 16px;

$btn-border-radius: 20px;

:root {
  --text-color: #333333;
  --color-white: #ffffff;
  --text-gray-color: #3d3d3d;
  --text-black-color: #000000;
  --primary-color: #ff8125;
  --primary-color-500: #f1631c;
  --primary-yellow: #fec224;
  --grey-200: #ececec;

  --border-color: #e0eff5;
  --border-radius: 4px;
  --box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
}
